<template>
  <div className="animated">
    <b-card>
      <b-card-header>
        <i class="icon-menu mr-1"></i>Inventory
      </b-card-header>
      <b-card-body>
        <v-client-table :columns="columns" :data="data"  :options="options" :theme="theme" id="dataTable">
          <router-link slot="_id" slot-scope="props" target="_blank" :to="`inventories/${props.row._id}`" class="icon-eye"></router-link>
        </v-client-table>
      </b-card-body>
    </b-card>
    <router-link :to="{ name: 'Create Inventory' }">
      <b-button type="submit" value="Add Inventory" variant="outline-dark"> Add Inventory </b-button>
    </router-link>
  </div>
  
</template>

<script>

    import Vue from 'vue'
  import {ClientTable, Event} from 'vue-tables-2'
  import axios from 'axios'


  Vue.use(ClientTable)

  export default {
    name: 'DataTable',
    components: {
      ClientTable,
      Event
    },
    data: function () {
      return {
        columns: ['product_id', 'Batch_No', 'UPC_code', 'shelf_life', 'status', 'warehouse','expiry_date','_id'],
        data: [],
        options: {
          headings: {
            product_id : 'Product ID', 
            Batch_No : 'Batch Number', 
            UPC_code : 'UPC Code', 
            shelf_life : 'Shelf Life', 
            status : 'Status', 
            warehouse : 'Warehouse',
            expiry_date : 'Expiration Date',
            _id : 'URL'
          },
          sortable: ['product_id', 'Batch_No', 'UPC_code', 'shelf_life', 'status', 'warehouse','expiry_date'],
          filterable: ['product_id', 'Batch_No', 'UPC_code', 'shelf_life', 'status', 'warehouse','expiry_date'],
          sortIcon: { base:'fa', up:'fa-sort-asc', down:'fa-sort-desc', is:'fa-sort' },
          pagination: {
            chunk: 5,
            edge: false,
            nav: 'scroll'
          }
        },
        useVuex: false,
        theme: 'bootstrap4',
        template: 'default'
      }
    },
    async mounted() {
    axios.get("https://selacious-cloud-siteapi.herokuapp.com/inventories")
    .then(response => {this.data = response.data})
  },
  };

// </script>

